* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
header {
  background-color: rgb(141, 68, 0);
  color: white;
  text-align: center;
  font-size: 2rem;
  padding: 1rem 0;
}
ul {
  margin: auto;
  max-width: 1200px;
  width: calc(100% - 2rem);
}
li {
  display: flex;
  justify-content: space-between;
}
ul > li > div {
  font-size: 1.25rem;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
}
.good {
  align-items: center;
}
.btn_good {
  border-radius: 0.5rem;
  border: none;
  padding: 0.2rem;
  background-color: bisque;
}

.modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: #00000060;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal .con {
  background-color: white;
  padding: 1rem;
  border-radius: 1rem;
  width: 50%;
  max-width: 500px;
  position: relative;
}
.modal h1 {
  text-align: center;
}
.modal p {
  padding: 1rem 0;
}
.close {
  position: absolute;
  top: 0;
  right: -50px;
}
.con {
  max-width: 600px;
  width: calc(100% - 2rem);
  margin: auto;
  background-color: #d9d9d9;
  border-radius: 1rem;
  display: flex;
  padding: 1.5rem;
  flex-direction: column;
}
